import validator from 'validator';

export default {

  login_rules: [
    {
      field: 'name',
      method: validator.isEmpty,
      validWhen: false,
      message: 'Voc precisa digitar seu nome.'
    },
    {
      field: 'email',
      method: validator.isEmpty,
      validWhen: false,
      message: 'Informa teu email, que vamos tentar te d um retorno o mais rpido possvel.'
    },
    {
      field: 'email',
      method: validator.isEmail,
      validWhen: true,
      message: 'Verifica se teu email est correto por favor.'
    },
    {
      field: 'subject',
      method: validator.isEmpty,
      validWhen: false,
      message: 'Informa pra gente um ttulo do motivo do teu contato.'
    },
    {
      field: 'message',
      method: validator.isEmpty,
      validWhen: false,
      message: 'Deixa aqui a mensagem que voc quer enviar pra gente.'
    }
  ]
};


